.forgot-password-page {
    display: flex;
    min-height: 88vh;
    font-family: Inter, system-ui, -apple-system, 'Segoe UI', Roboto, Arial
}

.forgot-left {
    flex: 1;
    padding: 64px 72px;
    display: flex;
    text-align: center;
    align-items: center;
    background: radial-gradient(600px 300px at 25% 30%, rgba(255, 140, 60, 0.04), transparent 25%), linear-gradient(90deg, #2a2740 0%, #213843 100%);
    color: #f1eee9;
}

.forgot-left-inner {
    text-align: left;
    width: 560px;
    padding: 36px 40px;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 18px;
}

.forgot-left h1 {
    font-size: 36px;
    margin: 0 0 12px;
    color: #fff9f6
}


.welcome-bullets {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    gap: 12px;
    align-items: flex-start;
}

.welcome-bullets li {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 12px;
    margin: 0;
}

.welcome-bullets .check {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 28px;
    height: 28px;
    background: #ff8a34;
    color: white;
    border-radius: 6px;
    font-weight: 700
}

.forgot-right {
    width: 420px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.45), rgba(0, 0, 0, 0.55));
    padding: 48px
}

.forgot-card {
    width: 100%;
    background: linear-gradient(180deg, #0b0b0b 0%, #081012 100%);
    border-radius: 12px;
    padding: 28px;
    box-shadow: 0 24px 80px rgba(3, 8, 12, 0.75);
    color: #f0eae3
}

.forgot-card .brand {
    font-weight: 700;
    color: #ffb386;
    margin-bottom: 6px;
}

.forgot-card h2 {
    margin: 6px 0 12px 0;
    text-align: center;
}

.muted {
    color: #c9bfb6;
    margin-bottom: 16px;
}

.small {
    font-size: 13px
}

.input-label {
    display: block;
    margin-bottom: 4px;
    color: #c9bfb6;
    font-size: 13px;
}

.email,
.password {
    width: 100%;
    padding: 10px 12px;
    height: 44px;
    border-radius: 8px;
    border: 1px solid rgba(255, 255, 255, 0.04);
    background: rgba(255, 255, 255, 0.02);
    color: white;
    margin-top: 8px;
    box-sizing: border-box;
}

.password-wrap {
    position: relative;
    display: flex;
    align-items: center;
}

.password-wrap .password {
    padding-right: 72px;
}

.password.shown {
    color: white;
}

.show-toggle {
    position: absolute;
    right: 12px;
    top: 30px;
    transform: translateY(-50%);
    height: 28px;
    padding: 0 8px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    background: rgba(255, 179, 134, 0.06);
    border: 1px solid rgba(255, 179, 134, 0.12);
    color: #ffb386;
    border-radius: 6px;
    cursor: pointer;
    font-size: 13px;
    white-space: nowrap;
}

.show-toggle:focus {
    outline: 2px solid rgba(255, 179, 134, 0.25);
}

.actions {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 10px
}

.remember {
    color: #c9bfb6
}

.forgot {
    color: #ffb386
}

.btn {
    padding: 10px 12px;
    border-radius: 8px;
    border: 1px solid rgba(255, 255, 255, 0.04);
    background: rgba(255, 255, 255, 0.02);
    color: inherit;
    cursor: pointer;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px
}

.btn.primary {
    background: #ff6b2b;
    color: white;
    border: none;
}

.full {
    width: 100%;
    margin: 12px auto;
}

.signup-link {
    margin-top: 14px;
    color: #c9bfb6;
    text-align: center;
    font-size: 14px;
}

.signup-link a {
    color: #ffb386
}

.username:focus,
.password:focus,
.password.shown:focus {
    border: 1px solid var(--accent) !important;
    outline: none !important;
    box-shadow: none !important;
}

.success {
    color: #28a745;
    text-align: center;
    margin-top: 1rem;
}

.error {
    color: #dc3545;
    text-align: center;
    margin-top: 1rem;
}

.back-link {
    margin-top: 20px;
    color: #c9bfb6;
    text-align: center;
    font-size: 14px;
}

.back-link a {
    color: #ffb386
}


@media(max-width:920px) {
    .forgot-password-page {
        flex-direction: column
    }

    .forgot-left {
        display: none
    }

    .forgot-right {
        width: 100%;
        padding: 20px
    }
}